html {
  scroll-behavior: smooth;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 18px;
  line-height: 1.9;
  background-color: #FFFFFF;
  color: #1B1B1B;
  min-height: 100vh;
  overflow-x: hidden;
}

.entry-content > *:not(.alignwide):not(.alignfull):not(.alignleft):not(.alignright):not(.is-style-wide) {
  max-width: 938px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: bold;
  font-family: "Space Grotesk", sans-serif;
}

h1 strong,
h2 strong,
h3 strong,
h4 strong,
h5 strong,
h6 strong {
  color: #FF252C;
}

h1 {
  font-size: 34px;
  line-height: 1.275;
}
@media screen and (min-width: 1024px) {
  h1 {
    font-size: 44px;
  }
}

h2 {
  font-size: 26px;
  line-height: 1.275;
}
@media screen and (min-width: 1024px) {
  h2 {
    font-size: 32px;
  }
}

h3 {
  font-size: 20px;
  line-height: 1.275;
}
@media screen and (min-width: 1024px) {
  h3 {
    font-size: 24px;
  }
}

h4 {
  font-size: 18px;
  line-height: 1.5;
}
@media screen and (min-width: 1024px) {
  h4 {
    font-size: 20px;
  }
}

label {
  font-size: 14px;
  line-height: 1.5;
  text-transform: none;
}
@media screen and (min-width: 1024px) {
  label {
    font-size: 16px;
  }
}
label + input {
  margin-top: 16px;
}

input[type=checkbox],
input[type=date],
input[type=datetime-local],
input[type=email],
input[type=file],
input[type=image],
input[type=month],
input[type=number],
input[type=password],
input[type=radio],
input[type=search],
input[type=tel],
input[type=text],
input[type=time],
input[type=url],
input[type=week],
textarea,
select {
  font-size: 14px;
  padding: 13px 19px;
  border: 1px solid #CCCCCC;
  border-radius: 4px;
}
@media screen and (min-width: 1024px) {
  input[type=checkbox],
input[type=date],
input[type=datetime-local],
input[type=email],
input[type=file],
input[type=image],
input[type=month],
input[type=number],
input[type=password],
input[type=radio],
input[type=search],
input[type=tel],
input[type=text],
input[type=time],
input[type=url],
input[type=week],
textarea,
select {
    font-size: 16px;
  }
}

textarea {
  resize: none;
  min-height: 200px;
}

input[type=submit] {
  font-size: 14px;
  line-height: 1.5;
  background-color: #FF252C;
  color: #FFFFFF;
  padding: 16px 32px;
  border-radius: 4px;
  text-transform: none;
  font-weight: 700;
  border: none;
  cursor: pointer;
  box-shadow: 0 0 0 0 #FFFFFF, 0 0 0 0 #FF252C;
  transition: box-shadow 0.3s ease-in-out;
}
input[type=submit]:hover, input[type=submit]:focus-visible {
  box-shadow: 0 0 0 2px #FFFFFF, 0 0 0 4px #FF252C;
}
input[type=submit][disabled] {
  cursor: not-allowed;
  opacity: 0.2;
  background-color: #1B1B1B;
}

.wp-block-button .wp-block-button__link {
  font-size: 14px;
  line-height: 1.5;
  background-color: #FF252C;
  color: #FFFFFF;
  padding: 16px 32px;
  border-radius: 4px;
  text-transform: none;
  font-weight: 700;
  border: none;
  cursor: pointer;
  box-shadow: 0 0 0 0 #FFFFFF, 0 0 0 0 #FF252C;
  transition: box-shadow 0.3s ease-in-out;
}
.wp-block-button .wp-block-button__link:hover, .wp-block-button .wp-block-button__link:focus-visible {
  box-shadow: 0 0 0 2px #FFFFFF, 0 0 0 4px #FF252C;
}
.wp-block-button .wp-block-button__link[disabled] {
  cursor: not-allowed;
  opacity: 0.2;
  background-color: #1B1B1B;
}

.is-style-outline .wp-block-button__link {
  color: #FF252C !important;
  border: 1px solid #FF252C !important;
}

.wp-block-columns.alignwide + *, * + .wp-block-columns.alignwide {
  margin-top: 100px;
}
.wp-block-columns + .wp-block-query {
  margin-top: 50px;
}

.wp-block-column > *:first-child {
  margin-top: 0 !important;
  padding-top: 0 !important;
}

.wp-block-column > *:last-child {
  margin-bottom: 0 !important;
  padding-bottom: 0 !important;
}

.wp-block-column.has-background p {
  font-size: 14px;
  line-height: 1.5;
}

.has-background.wp-block-column {
  padding: 48px 56px;
  border-radius: 4px;
}

.has-white-background-color.wp-block-column {
  box-shadow: 0 0 44px rgba(0, 0, 0, 0.05);
}

.wp-block-cover .has-vivid-red-background-color,
.wp-block-cover .has-accent-background-color {
  mix-blend-mode: multiply;
  opacity: 1;
  background-color: #FF252C;
}
.wp-block-cover .has-vivid-red-background-color ~ .wp-block-cover__inner-container,
.wp-block-cover .has-accent-background-color ~ .wp-block-cover__inner-container {
  color: #FFFFFF;
  padding-right: 0;
}
.wp-block-cover .wp-block-cover__inner-container {
  padding-top: 40px;
  padding-bottom: 40px;
  padding-left: var(--wp--custom--spacing--outer);
  padding-right: var(--wp--custom--spacing--outer);
}
.wp-block-cover.is-light .wp-block-cover__background {
  background-color: #FFFFFF;
}

* + .wp-block-media-text.alignfull, .wp-block-media-text.alignfull + * {
  margin-top: 80px;
}

.wp-block-navigation a {
  font-size: 14px;
  line-height: 1.5;
}

.wp-block-navigation-link.wp-block-button a {
  font-size: 14px;
  line-height: 1.5;
  background-color: #FF252C;
  color: #FFFFFF;
  padding: 16px 32px;
  border-radius: 4px;
  text-transform: none;
  font-weight: 700;
  border: none;
  cursor: pointer;
  box-shadow: 0 0 0 0 #FFFFFF, 0 0 0 0 #FF252C;
  transition: box-shadow 0.3s ease-in-out;
}
.wp-block-navigation-link.wp-block-button a:hover, .wp-block-navigation-link.wp-block-button a:focus-visible {
  box-shadow: 0 0 0 2px #FFFFFF, 0 0 0 4px #FF252C;
}
.wp-block-navigation-link.wp-block-button a[disabled] {
  cursor: not-allowed;
  opacity: 0.2;
  background-color: #1B1B1B;
}

.current-menu-item > .wp-block-navigation-item__content {
  font-weight: bold;
  color: #FF252C;
}

.has-background .is-style-outline {
  color: #FFFFFF;
}
.has-background .is-style-outline a {
  border-color: #FFFFFF;
}

.has-foreground-background-color {
  background-color: #1B1B1B;
  color: #FFFFFF;
}

.has-secondary-background-color {
  background-color: rgba(255, 37, 44, 0.03) !important;
}

.has-background {
  padding-top: 60px;
  padding-bottom: 60px;
}

.wordcamp-speaker {
  display: flex;
  flex-direction: column;
}

.wordcamp-speakers__title {
  font-size: 20px;
  line-height: 1.275;
  margin-top: 16px;
  margin-bottom: 0;
}
@media screen and (min-width: 1024px) {
  .wordcamp-speakers__title {
    font-size: 24px;
  }
}
.wordcamp-speakers__title a {
  text-decoration: none;
}

.wordcamp-image__avatar-link img {
  width: 100%;
  height: auto;
}
.wordcamp-image__avatar-container {
  order: -1;
}
.wordcamp-image__avatar-container img {
  aspect-ratio: 1/1;
  height: auto;
  width: 100%;
  object-fit: cover;
}

#tix {
  padding-top: 0;
}

.tix-tickets-list {
  width: 100%;
  border-collapse: collapse;
}
.tix-tickets-list thead {
  background-color: #F4F4F4;
}
.tix-tickets-list thead th {
  font-family: "Space Grotesk", sans-serif;
  font-weight: 300;
  padding: 16px 24px;
}
.tix-tickets-list th,
.tix-tickets-list td {
  padding-top: 30px;
  padding-bottom: 30px;
}

.tix-column-description {
  text-align: left;
}
.tix-column-price, .tix-column-quantity, .tix-column-remaining {
  text-align: center;
}

.tix-ticket-title {
  font-size: 16px;
  line-height: 1.5;
}
@media screen and (min-width: 1024px) {
  .tix-ticket-title {
    font-size: 18px;
  }
}
.tix-ticket-excerpt {
  font-size: 14px;
}
@media screen and (min-width: 1024px) {
  .tix-ticket-excerpt {
    font-size: 16px;
  }
}

.wordcamp-sponsors img {
  height: auto;
}
.wordcamp-sponsors .wordcamp-post-list__post {
  display: flex;
  align-items: center;
  justify-content: center;
}

.wp-block-post-template {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(4, min-content);
  gap: var(--wp--style--block-gap);
}

.wp-block-post {
  font-family: "Space Grotesk", sans-serif;
  position: relative;
  margin: 0;
  grid-column: 3/-1;
}
.wp-block-post a::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
}
.wp-block-post .wp-block-post-featured-image {
  margin-top: 0;
  margin-bottom: 32px;
  display: none;
}
.wp-block-post-date {
  font-size: 14px;
  line-height: 1.5;
  color: #FF252C;
}
.wp-block-post .wp-block-post-title {
  font-size: 18px;
  line-height: 1.5;
  margin: 0 !important;
  margin-top: 6px;
}
@media screen and (min-width: 1024px) {
  .wp-block-post .wp-block-post-title {
    font-size: 20px;
  }
}
.wp-block-post .wp-block-post-title a {
  color: #1B1B1B;
  text-decoration: none;
}
.wp-block-post-excerpt {
  font-size: 14px;
  line-height: 1.5;
  margin-top: 16px;
  display: none;
}

.wp-block-post + .wp-block-post {
  margin-top: 0;
}

.entry-content .wp-block-post-title {
  font-family: "Poppins", sans-serif;
}

.wp-block-post-featured-image {
  border-radius: 4px;
  overflow: hidden;
  max-width: 940px;
  margin-left: auto;
  margin-right: auto;
}

.wp-block-post-content img {
  max-width: 100%;
}

.wp-block-post-date {
  font-family: "Space Grotesk", sans-serif;
  font-size: 14px;
  line-height: 1.5;
}
.wp-block-post-date + .wp-block-post-title {
  margin-top: 12px;
}

.wp-block-post-title {
  font-family: "Space Grotesk", sans-serif;
  font-size: 34px;
  line-height: 1.275;
}
@media screen and (min-width: 1024px) {
  .wp-block-post-title {
    font-size: 44px;
  }
}

.wp-block-comments {
  width: 100%;
  max-width: 940px;
  margin-left: auto;
  margin-right: auto;
}
* + .wp-block-comments {
  margin-top: 60px !important;
  border-top: 1px solid #CCCCCC;
  padding-top: 60px;
}

.comment-reply-title {
  font-size: 18px;
  line-height: 1.5;
  margin-top: 0;
}
@media screen and (min-width: 1024px) {
  .comment-reply-title {
    font-size: 20px;
  }
}

.logged-in-as {
  font-size: 14px;
  line-height: 1.5;
}

.comment-subscription-form {
  margin: 0;
}

.admin-bar header.wp-block-template-part,
.admin-bar #site-header {
  top: 32px;
}

header.wp-block-template-part,
#site-header {
  padding-left: var(--wp--custom--spacing--outer);
  padding-right: var(--wp--custom--spacing--outer);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  background: none;
  padding-top: 40px;
  padding-bottom: 40px;
  z-index: 10;
}

header.wp-block-template-part .wp-block-group,
.header-inner {
  padding: 0;
  margin: 0;
  width: 100%;
}

footer.wp-block-template-part {
  font-family: "Poppins", sans-serif;
  font-size: 14px;
  line-height: 1.5;
  margin-top: 80px;
}
footer.wp-block-template-part .alignfull .alignfull {
  padding-left: var(--wp--custom--spacing--outer);
  padding-right: var(--wp--custom--spacing--outer);
}
