.wp-site-blocks {
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
.wp-site-blocks > * + * {
  margin-top: 0;
}
.wp-site-blocks main {
  flex: 1;
}

.entry-header {
  display: none;
}

.entry-content {
  font-family: "Poppins", sans-serif;
  padding-top: 200px;
}