@import "../mixins/colors";
@import "../mixins/typography";


h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: bold;
  @include font-headings();
}

h1,
h2,
h3,
h4,
h5,
h6 {

  strong {
    color: $red;
  }
}


h1 {
  @include h1();
}

h2 {
  @include h2();
}

h3 {
  @include h3();
}

h4 {
  @include h4();
}
